import { useEffect, useRef, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Typography } from '@material-ui/core';
import { useStyles } from './CommonDocument.style';
import { findCommonDocumentById } from '../../redux/actions';
import { Alert } from '@material-ui/lab';

import './CommonDocument.css';

const splitStringIntoWordsAndPunctuationMarks = str => {
  const words = [...str.matchAll(/[a-zа-яё-]+/gi)];
  const parts = [];

  for (let i = 0; i < words.length; i++) {
    const { index } = words[i];
    const [word] = words[i];
    const wordLength = word.length;

    parts.push({
      word,
    });

    let punctuationMarks = '';

    if (words[i + 1]) {
      punctuationMarks = str.substring(index + wordLength, words[i + 1].index);
    } else {
      punctuationMarks = str.substring(index + wordLength, str.length);
    }

    parts.push({
      punctuationMarks,
    });
  }

  return parts;
};

const randomInt = (min, max) => Math.floor(Math.random() * (max - min + 1) + min);

export const CommonDocument = () => {
  const dispatch = useDispatch();
  const appState = useSelector(state => state.app);
  const classes = useStyles();
  const params = useParams();
  const [document_, setDocument_] = useState(null);
  const canvas = useRef(null);
  const animation = useRef(null);

  useEffect(() => {
    if (appState.data && (!document_ || document_.id !== params.documentId)) {
      setDocument_(dispatch(findCommonDocumentById(params.documentId)));
    } else if (canvas.current && !animation.current) {
      const canvas_ = canvas.current;
      const c = canvas_.getContext('2d');
      const { width, height, top } = canvas_.parentElement.getBoundingClientRect();

      canvas_.width = width;
      canvas_.height = height;

      const words = [...document.querySelectorAll('.word')];
      const path = words.reduce((prevPath, word) => {
        const { x, y, width, height } = word.getBoundingClientRect();
        const r = (width / canvas_.width) * 15 + 5;

        return [
          ...prevPath,
          {
            x: randomInt(x + r, x + width - r),
            y: randomInt(y - top, y + height - top),
            r,
          },
        ];
      }, []);
      let pathIndex = 1;
      const circle = { x: path[0].x, y: path[0].y, r: 15 };
      let vx = 0;
      let vy = 0;
      const steps = 25;
      let stepIndex = 0;
      const classes_ = ['good', 'medium', 'bad'];

      const renderPath = () => {
        c.clearRect(0, 0, width, height);

        if (!words[pathIndex - 1].classList.contains('seen')) {
          words[pathIndex - 1].classList.add('seen');
          words[pathIndex - 1].classList.add(classes_[randomInt(0, classes_.length - 1)]);
        }

        if (stepIndex === 0) {
          const dx = path[pathIndex].x - circle.x;
          const dy = path[pathIndex].y - circle.y;

          vx = dx / steps;
          vy = dy / steps;
          stepIndex++;
        } else if (stepIndex === steps - 1) {
          pathIndex++;
          stepIndex = 0;
        } else {
          circle.x += vx;
          circle.y += vy;
          stepIndex++;
        }

        c.fillStyle = 'rgba(60, 120, 216, 0.5)';
        c.strokeStyle = 'rgba(60, 120, 216, 0.7)';
        c.lineWidth = 1;

        c.beginPath();
        c.arc(circle.x, circle.y, circle.r, 0, Math.PI * 2);
        c.closePath();
        c.fill();
        c.stroke();

        // c.strokeStyle = 'rgba(60, 120, 216, 0.6)';
        // c.lineWidth = 2;

        // for (let i = 0; i < pathIndex - 1; i++) {
        //   const p = path[i];
        //   const p_ = path[i + 1];

        //   c.beginPath();
        //   c.moveTo(p.x, p.y);
        //   c.lineTo(p_.x, p_.y);
        //   c.stroke();
        //   c.closePath();
        // }

        c.fillStyle = 'rgba(60, 120, 216, 0.3)';
        c.strokeStyle = 'rgba(60, 120, 216, 0.6)';
        c.lineWidth = 1;

        for (let i = 0; i < pathIndex; i++) {
          const p = path[i];

          c.beginPath();
          c.arc(p.x, p.y, p.r, 0, Math.PI * 2);
          c.closePath();
          c.fill();
          // c.stroke();
        }

        if (pathIndex < 30) {
          animation.current = requestAnimationFrame(renderPath);
        } else {
          cancelAnimationFrame(animation);
        }
      };

      // renderPath();
    }
  }, [appState.data, document_, params.documentId, dispatch]);

  const renderParagraph = (p, i) => {
    const parts = splitStringIntoWordsAndPunctuationMarks(p);

    return (
      <Typography key={i} className={classes.textSection} variant="body1">
        {parts.map((part, partIndex) =>
          part.word ? (
            <span key={partIndex} className="word">
              {part.word}
            </span>
          ) : (
            <span key={partIndex}>{part.punctuationMarks}</span>
          )
        )}
      </Typography>
    );
  };

  return document_ ? (
    <div className={classes.root}>
      <Alert severity="info">
        На данной странице представлена имитация просмотра документа сотрудником с учетом отслеживания его взгляда
        &mdash; так это «видит» система мониторинга
      </Alert>
      <Typography className={classes.title} variant="h6">
        {document_.name}
      </Typography>
      <div className={classes.text}>
        <canvas ref={canvas} style={{ position: 'absolute', left: 0, top: 0 }} />
        {document_.text.split('\n').map((paragraph, index) => renderParagraph(paragraph, index))}
      </div>
    </div>
  ) : null;
};
