import React from 'react';
import { Typography, Button, Paper } from '@material-ui/core';
import { KeyboardArrowRight } from '@material-ui/icons';
import { Alert } from '@material-ui/lab';
import { icon } from '@fortawesome/fontawesome-svg-core';
import { faReact } from '@fortawesome/free-brands-svg-icons';
import { useStyles } from './Atom.style';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';

const reactIcon = icon(faReact).icon;

export const Atom = () => {
  const classes = useStyles();
  const appState = useSelector(state => state.app);
  const [taskCategories, setTaskCategories] = useState(null);

  useEffect(() => {
    if (appState.data.tasks && !taskCategories) {
      const categories = {};

      appState.data.tasks.forEach(task => {
        const { category } = task;

        if (!categories[category]) {
          categories[category] = [];
        }

        categories[category].push({ ...task });
      });

      setTaskCategories(categories);
    }
  }, [appState.data.tasks, taskCategories]);

  return taskCategories ? (
    <div className={classes.root}>
      <Alert severity="info">
        АТОМ – это внутренняя финансовая система и валюта Росатома.
        <br />
        <br />
        Накапливайте атомы за выполнение заданий в приложении и капитализируйте их в реальные полезные вещи!
      </Alert>
      <div className={classes.balance}>
        <Typography>Ваш баланс</Typography>
        <Typography className={classes.atoms} variant="h4">
          <span>100</span>
          <span>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox={`0 0 ${reactIcon[0]} ${reactIcon[1]}`} height="30px">
              <path d={reactIcon[4]} />
            </svg>
          </span>
        </Typography>
      </div>
      <Button className={classes.button} variant="outlined" color="primary" fullWidth>
        <div>Перейти в магазин</div>
        <KeyboardArrowRight />
      </Button>
      <div className={classes.tasks}>
        <Typography gutterBottom>Доступные задания</Typography>
        {Object.entries(taskCategories).map(([category, tasks]) => (
          <div key={category} className={classes.category}>
            <Typography gutterBottom color="textSecondary">
              {category}
            </Typography>
            {tasks.map(task => (
              <Paper key={task.name} className={classes.task} variant="outlined">
                <span>
                  <Typography variant="body1" color="primary">
                    {task.name}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    {task.desc}
                  </Typography>
                </span>
                <div>
                  <Typography className={classes.atoms} variant="body2">
                    <span>{task.price}</span>
                    <span>
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox={`0 0 ${reactIcon[0]} ${reactIcon[1]}`}
                        height="14px"
                      >
                        <path d={reactIcon[4]} />
                      </svg>
                    </span>
                  </Typography>
                </div>
              </Paper>
            ))}
          </div>
        ))}
      </div>
    </div>
  ) : null;
};
