import React from 'react';
import { useStyles } from './AdaptationResult.style';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import { Avatar, Divider, Typography } from '@material-ui/core';
import { Alert } from '@material-ui/lab';

const randomInt = (min, max) => Math.floor(Math.random() * (max - min) + min);
const colors = ['#3c78d8', '#6d9eeb', '#a4c2f4'];
const msg = ['профессиональной', 'организационной', 'социально-психологической'];

export const AdaptationResult = () => {
  const classes = useStyles();
  const appState = useSelector(state => state.app);
  const [results, setResults] = useState(null);
  const [chart, setChart] = useState(null);

  useEffect(() => {
    if (appState.data.employees && !results) {
      let res = [];
      let maxSum = 0;

      appState.data.employees.forEach(employee => {
        const categories = [];
        let sum = 0;

        for (let i = 0; i < 3; i++) {
          const r = randomInt(10, 100);

          categories.push(r);
          sum += r;
        }

        if (sum > maxSum) {
          maxSum = sum;
        }

        res.push({
          employee,
          categories,
          sum,
        });
      });

      res = res.sort((r1, r2) => r2.sum - r1.sum);

      setResults(res);
      setChart({ maxSum });
    }
  }, [appState.data.employees, results]);

  return results && chart ? (
    <div className={classes.root}>
      <Alert severity="info">
        На данной странице отображаются гистограммы с накоплением, символизирующие результативность адаптации каждого
        сотрудника по основным видам адаптации
      </Alert>
      <div className={classes.legend}>
        <div className={classes.legendItem}>
          <span style={{ background: colors[0] }}></span>
          <Typography variant="body2">Профессиональная адаптация</Typography>
        </div>
        <div className={classes.legendItem}>
          <span style={{ background: colors[1] }}></span>
          <Typography variant="body2">Организационная адаптация</Typography>
        </div>
        <div className={classes.legendItem}>
          <span style={{ background: colors[2] }}></span>
          <Typography variant="body2">Социально-психологическая адаптация</Typography>
        </div>
      </div>
      <div className={classes.graph}>
        {results.map((result, resultIndex) => {
          const alert = {
            severity: '',
            message: [],
          };

          if (result.sum / chart.maxSum > 0.5) {
            let warning = false;

            for (let i = 0; i < result.categories.length; i++) {
              if (result.categories[i] / result.sum < 0.25) {
                warning = true;
                alert.message.push(`Низкий уровень ${msg[i]} адаптации`);
              }
            }

            if (warning) {
              alert.severity = 'warning';
            } else {
              alert.severity = 'success';
              alert.message.push('Высокий общий уровень адаптации');
            }
          } else {
            alert.severity = 'error';
            alert.message.push('Низкий общий уровень адаптации');
          }

          return (
            <React.Fragment key={result.employee.id}>
              <div className={classes.employee}>
                <Avatar className={classes.avatar} src={result.employee.image} />
                <div className={classes.lineContainer}>
                  <div className={classes.line} style={{ width: `calc(100% * ${result.sum / chart.maxSum})` }}>
                    {result.categories.map((category, index) => (
                      <div
                        className={classes.category}
                        style={{ width: `calc(100% * ${category / result.sum})`, background: colors[index] }}
                      >
                        {category / result.sum > 0.25 ? (
                          <Typography variant="body2">{((category / result.sum) * 100).toFixed(0)}%</Typography>
                        ) : (
                          <Typography variant="body2">...</Typography>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              </div>
              <Alert className={classes.alert} severity={alert.severity} variant="standard">
                {alert.message.join('; ')}
              </Alert>
              {resultIndex < results.length - 1 && <Divider />}
            </React.Fragment>
          );
        })}
      </div>
    </div>
  ) : null;
};
